<template>
  <div>    
    <l-map
      style="height: 100%;, width: 100%"
      :center="center"
      :zoom="zoom"
      :zoomAnimation=true
      :options="mapOptions"
      class="map"
      ref="map"
      @update:zoom="zoomUpdated"
      @update:center="centerUpdated"
      @update:bounds="boundsUpdated"
    > 

      <LMarker
        :key="markers[0].id"
        :lat-lng="markers[0].coordinates"
        @click="openP(0)"
        :icon="customIcon" />

      <LMarker
        :key="markers[1].id"
        :lat-lng="markers[1].coordinates"
        @click="openP(1)"
        :icon="customIcon" />

      <LMarker
        :key="markers[2].id"
        :lat-lng="markers[2].coordinates"
        @click="openP(2)"
        :icon="customIcon" />

      <LMarker
        :key="markers[3].id"
        :lat-lng="markers[3].coordinates"
        @click="openP(3)"
        :icon="customIcon" />

      <LMarker
        :key="markers[4].id"
        :lat-lng="markers[4].coordinates"
        @click="openP(4)"
        :icon="customIcon" />

      <pop1 :show="popVal1" @close="closeP" class="popup" :class="{'show':popVal1}" />
      <pop2 :show="popVal2" @close="closeP" class="popup" :class="{'show':popVal2}" />
      <pop3 :show="popVal3" @close="closeP" class="popup" :class="{'show':popVal3}" />
      <pop4 :show="popVal4" @close="closeP" class="popup" :class="{'show':popVal4}" />
      <pop5 :show="popVal5" @close="closeP" class="popup" :class="{'show':popVal5}" />

      <l-tile-layer :url="url" />

      <input type="button" value="내 위치" @click="goToMyLocation" class="my-location-button">
      <input type="button" value="탐방로" @click="goToCenter" class="center-button">

    </l-map>
  </div>
</template>
   
<script>
import { LMap, LMarker, LTileLayer } from 'vue2-leaflet';
import { Icon } from 'leaflet';
import L from 'leaflet';

import pop1 from '../components/popView1.vue';
import pop2 from '../components/popView2.vue';
import pop3 from '../components/popView3.vue';
import pop4 from '../components/popView4.vue';
import pop5 from '../components/popView5.vue';

import markerImg from 'leaflet/dist/images/marker-icon.png';
import markerShadowImg from 'leaflet/dist/images/marker-shadow.png';
import markerRetinaImg from 'leaflet/dist/images/marker-icon-2x.png';

import 'leaflet/dist/leaflet.css';

export default {
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      center: [ 37.408473, 128.045787 ], // 처음 지도의 중심이 되는 좌표
      zoom: 14, // 확대 레벨
      previousZoom : null, //현재의 확대 레벨
      bounds: null, //지도 경계가 변경될 때 발생하는 이벤트(드래그되거나 확대/축소 시 발생)

      popVal1 : false, //pop1이 열려있나 닫혀있나 여부 저장(default=false)
      popVal2 : false,
      popVal3 : false,
      popVal4 : false,
      popVal5 : false,

      isZoomIn: false, //줌인이 된 상태인지 아닌지 확인

      mapOptions: {
        minZoom: 14, //최소 줌 레벨 설정
        maxZoom: 18  //최대 줌 레벨 설정
      },
      markers:[ // 아이콘 포인트 표시 지정
        {id:1, coordinates:[37.414726, 128.050078], name: '체험학습관',
          imageUrl: require('@/assets/ggomi.png')}, // 체험학습관
        {id:2, coordinates:[37.408449, 128.045788], name: '구룡야영장',
          imageUrl: require('@/assets/ggomi.png')}, // 구룡야영장
        {id:3, coordinates:[37.40535807810431, 128.048564036553], name: '황장숲목길',
          imageUrl: require('@/assets/ggomi.png')}, // 황장숲목길
        {id:4, coordinates:[37.399378, 128.049886], name: '구룡사',
          imageUrl: require('@/assets/ggomi.png')}, // 구룡사
        {id:5, coordinates:[37.395018, 128.053136], name: '자생식물관찰원',
          imageUrl: require('@/assets/ggomi.png')}, // 자생식물관찰원
      ],
      customIcon: new Icon({  // 지도의 마커 사용자 지정 아이콘 사용
        iconUrl: require('@/assets/ggomi.png'),
        // iconSize: [50, 50],
        iconSize: [50, 50],
        iconAnchor: [25,25]
      }),
    };
  },
  components: {
    LMap,
    LTileLayer,
    pop1,
    pop2,
    pop3,
    pop4,
    pop5,
    LMarker,
},
  mounted(){
    delete Icon.Default.prototype._getIconUrl;
    Icon.Default.mergeOptions({ //marker-icon-2x.png 이미지 파일을 못찾는 에러 제거 코드
      iconRetinaUrl: markerRetinaImg,
      iconUrl: markerImg,
      shadowUrl: markerShadowImg,
    });

    // 현재 내 위치 추가를 위한 코드
    // this.$refs.map.mapObject.locate({setView: true, maxZoom:16});
    this.$refs.map.mapObject.on('locationfound', (e) =>{
      console.log('Location found: ', e); //위치를 찾았을 때 로깅 추가
      const radius = 100; //일단 100미터로 고정
      new L.Marker(e.latlng).addTo(this.$refs.map.mapObject).bindPopup('내위치').openPopup();
      new L.Circle(e.latlng, radius).addTo(this.$refs.map.mapObject);
    });
    this.$refs.map.mapObject.on('locationerror', (e) => {
      console.log('Location error: ', e.message); //에러 메세지 자세히 출력
    })

  },
  methods: {
    zoomUpdated (zoom) {
      this.zoom = zoom;
      // console.log(this.markers)
    },
    centerUpdated (center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    openP(index){
      if(! this.isZoomIn){
        this.previousZoom = this.zoom;
        // 먼저 줌인 액션 실행
        const marker = this.markers[index];
        this.$refs.map.mapObject.setZoomAround(marker.coordinates, 36);
        this.isZoomIn = true;
      }
      // 0.8초 뒤 팝업 열기
      setTimeout(()=>{
        this['popVal' + (index+1)] = true;
      }, 800); 
    },
    closeP(){
      this.popVal1 = false;
      this.popVal2 = false;
      this.popVal3 = false;
      this.popVal4 = false;
      this.popVal5 = false;

      if (this.isZoomIn) {
        // 팝업이 닫힌 후 줌 아웃(원래 줌 레벨로 돌아가기)
        this.$refs.map.mapObject.setZoom(this.previousZoom);
        this.isZoomIn = false;
      }
    }, 
    goToMyLocation(){
      this.$refs.map.mapObject.locate({
        setView : true,
        maxZoom : 20
      });
    }, 
    goToCenter(){
      this.$refs.map.mapObject.setView([37.408473, 128.045787],14);
    },  
  }
}
</script>

<style>
.map {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow :hidden
}
.marker-ggomi-icon {
  height: 50px;
  width: auto;
}
.icon{
  height:50px;
  width: 50px;
}
.popup{
  /* 트랜지션 속성을 추가하여 부드러운 나타남 및 사라짐 만들기 */
  transition: opacity 0.5s; 
  opacity: 0; /*초기에 투명상태로 설정*/
}
.popup.show{
  opacity: 1; /*나타날 때 투명도를 1로 설정하여 부드럽게 나타나게 함*/
} 
.my-location-button {
    z-index: 1001;
    position: absolute;
    bottom: 150px;
  }
  .center-button {
    z-index: 1001;
    position: absolute;
    bottom: 100px;
  }

</style>